.hQtIPa_web-content {
  width: 80%;
  margin: 48px auto auto;
}

.hQtIPa_introduce {
  width: 80%;
  height: auto;
  margin: auto;
}

.hQtIPa_logos a {
  width: 210px;
  height: 70px;
  padding: .2em .8em;
}

.hQtIPa_footer {
  text-align: center;
  box-sizing: border-box;
  background-color: #f7fbfd;
  width: 100%;
  padding: 32px 10%;
}

.hQtIPa_footer p, .hQtIPa_footer a {
  color: #444;
  font-size: 14px;
}

.hQtIPa_qrcode {
  width: 160px;
  margin: 15px;
  display: inline-block;
}

@media (width <= 768px) {
  .hQtIPa_logo img {
    width: 140%;
    margin-left: -20%;
  }

  .hQtIPa_introduce {
    padding: 0 0 20px;
  }

  .hQtIPa_introduce p {
    font-size: 12px;
    line-height: 18px;
  }

  .hQtIPa_logos ul, .hQtIPa_logos ul li {
    margin: 10px 0;
  }
}

@media (width <= 480px) {
  .hQtIPa_web-content {
    width: 90%;
    margin-top: 36px;
  }

  .hQtIPa_logos a {
    width: 140px;
    height: 50px;
  }

  .hQtIPa_logos img {
    max-width: 140px;
    max-height: 50px;
  }
}
